* {
    font-family: Arial, Helvetica, sans-serif;
}

body {
    padding: 60px;
}

input {
    color: grey;
    font-size: 1.2rem;
    width: 100%;
}

.card {
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 4px;
    margin-right: 10px;
    height: 230px;
    width: 230px;
    margin-left: 5px;
}

.h5 {
    padding: 5px;
    text-align: center;
}

.img {
    max-height: 180px;
    max-width: 180px;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}